import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import Pedido from './views/pedidos/Pedido'
import Pedidos from './views/pedidos/Pedidos'
import Produto from './views/produtos/Produto'
import Produtos from './views/produtos/Produtos'
import Regiao from './views/regioes/Regiao'
import Regioes from './views/regioes/Regioes'
import Promocao from './views/promocoes/Promocao'
import Promocoes from './views/promocoes/Promocoes'
import Login from './views/login/Login'
import PageNotFound from './views/PageNotFound'
    
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'inicio',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/pedidos',
      name: 'pedidos',
      component: Pedidos
    },
    {
      path: '/pedido/:id/',
      name: 'pedido',
      component: Pedido
    },
    {
      path: '/produtos/',
      name: 'Produtos',
      component: Produtos,
      children: [{
        path: '/produto/:id/:criar_editar(editar|criar)?',
        name: 'produto',
        component: Produto, //Produto
        meta: {
          algo: 'meu pintooooooo'
        },
        //props: true
      }],
    },
    {
      path: '/regioes_entrega',
      name: 'regioes_entrega',
      component: Regioes
    },
    {
      path: '/regiao/:id/:criar_editar(editar|criar)?',
      name: 'regiao',
      component: Regiao
    },
    {
      path: '/promocoes',
      name: 'promocoes',
      component: Promocoes
    },
    {
      path: '/promocao/:id/:criar_editar(editar|criar)?',
      name: 'promocao',
      component: Promocao
    },
    { path: "*", component: PageNotFound }
  ]
})
